

import React, { useState } from "react"; 
import { useForm } from "react-hook-form"; 
import { isObjEmpty } from "../../../../utility/Utils";
import useJwt from "../../../../auth/jwt/useJwt";
import { Button, Card, CardBody, CardHeader, CardText, Col, Form, FormGroup, Input, Label } from "reactstrap";
 import { useTranslation } from "@hooks/useTranslation";
 import * as Yup from "yup";
import { useConfigs } from "../../../../utility/hooks/useConfigs";
import { ErrorMessage, Field, Formik, useFormik } from "formik";
import ReCAPTCHA from "react-google-recaptcha";
import classnames from "classnames";
import { useHistory, Link } from "react-router-dom";
import   "./index.scss";
import { FaEye, FaEyeSlash } from "react-icons/fa";
import { handleLogin } from "../../../../redux/actions/auth";
import { useDispatch } from "react-redux";
import jwtDefaultConfig from "../../../../@core/auth/jwt/jwtDefaultConfig";

const Login = () => { 
  const history = useHistory(); 
  const [errorMessage, setErrorMessage] = useState(null);
  const {  errors,   } = useForm();
   const dispatch = useDispatch();
  const { t } = useTranslation();
    const [showPassword, setShowPassword] = useState(false);
  const configs = useConfigs();
  const baseURL =   configs.REACT_APP_API_HOST  ; 
  const [isSending, setIsSending] = useState(false); // لحالة إرسال الطلب
   const [captchaVerified, setCaptchaVerified] = useState(false);
    const company = localStorage.getItem("company");
    const databaseId = "201";
 
  // تحديد اللون بناءً على قيمة الشركة
  const color = company == 1 ? "success" : "secondary";
  const color1=company == 1 ? '#008086' : '#005C93';

  // تحديد صورة الشركة بناءً على Local Storage
  const companyLogo =
    company === "2"
      ? "/assets/images/logo2.png"
      : "/assets/images/logo1.png";
   const cover = 
  company === "2"
      ?"/assets/images/cover-2.png"
      :"/assets/images/cover-1.png";

      const title=company==1? "/assets/images/titlelogin1.png":"/assets/images/titlelogin2.png"
        const validationSchema = Yup.object({
             username: Yup.string().required('اسم المستخدم مطلوب'),
             password: Yup.string()
               .required('كلمة المرور مطلوبة')
               .min(6, 'يجب أن تكون كلمة المرور 6 أحرف على الأقل'), 
           });
        
      const onCaptchaVerify = (value) => {
        if (value) {
          setCaptchaVerified(true);
        } else {
          setCaptchaVerified(false);
        }
      };


      const formik = useFormik ({
          initialValues:{  username: null,password: null },
          validationSchema:validationSchema,
           onSubmit:(values) => {

            if (isObjEmpty(errors)) {
              useJwt
                .login({
                  loginEndpoint: baseURL+"Auth/login",
                  email: values.username,
                  password: values.password,
                  companyId:company,
                  appKey:"aeTzSTKBOOp0sLEObLtam70WnWrQsMwKvPZmToJOLP8mkvLibQeNsu5poT2CyeCvjqa0WD0JEbQjyByBEqVDU4Yn109yHS7o7ZMXj2V5SeOBvpcJRm5Pf1g1L6cOhNHV",
                  databaseId:databaseId
                })
                .then((res) => {
                  if (res.status === 200) {
                    console.log(res?.data.data=="");
                    
                         if(res?.data.data!=""){
                         localStorage.setItem("OTP",res?.data?.data)
                         localStorage.setItem("username",values?.username) 
                         localStorage.setItem("phone",values?.password) 
                         history.push("/ConfirmCode");}
                         else{
                          setErrorMessage(
                            res?.data?.message
                          );
                        }
                        //  window.location.reload();
          //              }else{
          //                  localStorage.setItem(
          //    jwtDefaultConfig.storageTokenKeyName,
          //    JSON.stringify(res.data.data.token.result)
          //  );
          //    dispatch(handleLogin(JSON.stringify(res)));
          //    // history.push("/ConfirmCode");
          //    window.location.reload();
                       
                        
          //            }
          }
                     else{
                       setErrorMessage(
                         res?.data?.message
                       );
                     }
                })
                .catch(( response ) => {
                  setErrorMessage(
                  "اسم المستخدم او كلمة المرور غير صحيح"
                  );
                });
            }
           }});
  return (
    <div className="login1">
      <div
        className="login-wrapper-background"
        style={{
          backgroundImage: `url(${cover})`,
          height: "100vh",
        }}
      >
        <div className="login-wrapper" >
          <div className="login-body">
             <div
  className="text-center"
  style={{
    // backgroundColor: "#e9e9e9",
    padding: "20px",
    borderRadius: "8px",
    // boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
  }}
>
  {/* Header */}
  <div style={{ marginBottom: "0px" }}>
    <img id="img2" src={companyLogo} style={{ height: "100px" }} />
  </div>

  {/* Body */}
  <div style={{marginBottom:"20px"}}>
  <img id="img2" src={title} style={{ height: "50px" }} />
  
    {/* <p
      style={{
        textAlign: "center",
        fontSize: "30px",
        fontWeight: "bolder",
        paddingTop: "1%",
        color: color1,
        marginBottom: "40px",
      }}
    >
اهلا بك في شركة إثراء الخير    </p> */}

    {/* Username Input */}
    <Col sm={12} style={{padding:"0"}} >
      <FormGroup>
        <Label for="userName">{t("userName")}</Label>
        <Input
          style={{ textAlign: "center" }}
          type="text"
          placeholder={"ادخل اسم المستخدم"}
          autoComplete="off"
          {...formik.getFieldProps("username")}
        />
        {formik.touched.username && formik.errors.username ? (
          <small className="text-danger">{formik.errors.username}</small>
        ) : null}
      </FormGroup>
    </Col>

    {/* Password Input */}
    <Col sm={12} style={{padding:"0"}} >
      <FormGroup>
        <Label for="password">كلمة المرور</Label>
        <Input
          style={{ textAlign: "center", paddingRight: "35px" }}
          type={showPassword ? "text" : "password"}
          placeholder={"ادخل كلمة المرور"}
          autoComplete="off"
          {...formik.getFieldProps("password")}
        />
        <span
          id="showPassword"
          onClick={() => setShowPassword(!showPassword)}
          style={{
            position: "absolute",
            left: "25px",
            top: "70%",
            transform: "translateY(-50%)",
            cursor: "pointer",
            color: color1,
          }}
        >
          {showPassword ? (
            <FaEyeSlash size={"20px"} style={{ color: color1 }} />
          ) : (
            <FaEye size={"20px"} style={{ color: color1 }} />
          )}
        </span>
        {formik.touched.password && formik.errors.password ? (
          <small className="text-danger">{formik.errors.password}</small>
        ) : null}
      </FormGroup>
    </Col>

    {/* Forgot Password Link */}
    <div>
      <p className="text-left mt-2">
        {/* <Link to="/ForgotPassword" style={{ color: color1 }}> */}
        <Link to="#" style={{ color: color1 }}>
          <span>{t("ForgotPassword")}</span>
        </Link>
      </p>
    </div>

    {/* reCAPTCHA */}
    {/* <div className="mb-2" style={{width:"100%",direction:"rtl"}}>
      <ReCAPTCHA
        // className="form-control"
        hl="ar"
        sitekey="6Ld0pYMqAAAAALkmPGIDVHDsFcl9Lw-jKEbDG0q9"
        onChange={(value) => onCaptchaVerify(value)}
      />
    </div> */}
<div className="mb-2" style={{ width: "100%", direction: "rtl", display: "flex", justifyContent: "center" }}>
  <div style={{ width: "100%" }}> {/* ضبط الحجم ليكون متناسقًا مع الـ Input */}
    <ReCAPTCHA
      hl="ar"
      sitekey="6Ld0pYMqAAAAALkmPGIDVHDsFcl9Lw-jKEbDG0q9"
      onChange={(value) => onCaptchaVerify(value)}
      style={{ width: "100%" }}
    />
  </div>
</div>
    {/* Submit Button */}
    <Button 
      color={color}
      block
      type="submit"
      onClick={formik.handleSubmit}
      disabled={!captchaVerified || isSending}
    >
      {isSending ? "جاري الإرسال..." : " تسجيل الدخول"}
    </Button>
  {/* Error Message */}
  <p
      style={{
        textAlign: "center",
        color: "#c00",
        textDecoration: "underline #c22",
        fontWeight: "bolder",
        fontSize: "larger",
        marginBottom: "20px",
      }}
    >
      {errorMessage}
    </p>
    {/* Register Link */}
    <br />
    <p className="text-center mt-2">
    <span className='mr-25'> ليس لديك حساب؟ </span>

      {/* <Link to="/register" style={{ color: color1 }}> */}
      <Link to="#" style={{ color: color1 }}>
        <span>تسجيل مستخدم جديد</span>
      </Link>
    </p>

  
  </div>
</div>
          </div>
          <div style={{ 
   width: "50%", 
    padding: "0" 
}}>
   <img
        src="/assets/images/sada-logo.png"
         style={{
          position: "absolute",  // وضع الصورة بشكل مطلق داخل الحاوية
          right: "50px",  // نقل الصورة إلى أقصى اليمين
          bottom: "20px", // نقل الصورة إلى الأسفل
          height:   "5%",
          width:   "15%",
          padding: "0",
          borderRadius: "10px",
        }}
   />
</div>
        </div>
      </div>
      {/* <div style={{ height: "50vh", backgroundColor: "white" }}></div> */}
    </div>
  );
};

export default Login;